#nameInput {
  position: relative;
  left: 2rem;
}

#numberInput {
  position: relative;
  left: 1rem;
}

ul {
  list-style: decimal;
}

h1 {
  color: green;
  font-style: italic;
}

li {
  color: grey;
  padding-top: 3px;
  font-size: 16px;
}

.error {
  background-color: #f6d7da;
  color: #832b35;
  border-radius: 7px;
  font-size: 1rem;
  padding: 10px;
  margin-bottom: 30px;
}

.success {
  background-color: #d4edda;
  color: #626024;
  border-radius: 7px;
  font-size: 1rem;
  padding: 10px;
  margin-bottom: 30px;
}
